<template>
  <m-card-wrap
    content-orientation="horizontal"
    class="m-person-profile m-team-list__person"
  >
    <div v-if="photo && displayPhoto">
      <img
        :src="photo"
        class="m-person-profile__img img-responsive"
        :alt="`Headshot of ${name}`"
        loading="lazy"
      />
    </div>
    <div>
      <div class="m-person-profile__name">
        {{ name }}
      </div>
      <div v-if="title && displayTitle" class="m-person-profile__title">
        {{ title }}
      </div>
      <m-button
        v-if="url && displayUrl"
        :href="url"
        target="_blank"
        rel="noreferrer"
        link-style="external-link"
        tag="a"
      >
        LinkedIn
      </m-button>
    </div>
  </m-card-wrap>
</template>

<script>
import MButton from './MButton'
import MCardWrap from './MCardWrap'

export default {
  name: 'MTeamMember',
  components: { MButton, MCardWrap },
  props: {
    title: String,
    url: String,
    photo: String,
    type: {
      type: String,
      default: () => 'full',
    },
    name: {
      type: String,
      required: true,
    },
  },

  computed: {
    displayPhoto() {
      if (this.type === 'full' || this.type === 'noUrl') {
        return true
      }
      return false
    },
    displayTitle() {
      if (this.type !== 'nameOnly') {
        return true
      }
      return false
    },
    displayUrl() {
      if (this.type !== 'noUrl' && this.type !== 'nameOnly') {
        return true
      }
      return false
    },
  },
}
</script>

<style lang="scss">
@import '../sass/framework/framework.scss';

.m-person-profile {
  &__img {
    border-radius: 50%;
    margin-right: $pad-h--xl;
    min-width: 75px;
    width: 75px;

    @media screen and (max-width: 360px) {
      margin-right: $pad-h--md;
      min-width: 50px;
      width: 50px;
    }
  }

  &__name {
    font-size: $font-size-small;
    font-weight: $font-weight--semibold;
    line-height: 1.4;
  }

  &__title {
    font-size: $font-size-small;
    line-height: 1.4;
    padding-top: 2px;
  }
}

.m-team-list {
  // Layout uses flex width clac of width - $gutter
  // Parent has negative margin to allow each item to have even padding

  display: flex;
  flex-wrap: wrap;

  @include breakpoint(xs-up) {
    margin-left: -$gutter;
    margin-right: -$gutter;
  }

  &__person {
    margin-top: $pad-v--lg;
    width: 100%;

    @include breakpoint(xs-up) {
      margin-left: $gutter / 2;
      margin-right: $gutter / 2;
      width: calc(50% - #{$gutter});
    }

    @include breakpoint(md-up) {
      width: calc(33.333% - #{$gutter});
    }

    @include breakpoint(lg-up) {
      width: calc(25% - #{$gutter});
    }
  }
}
</style>
