<template>
  <div v-if="tabs.length" class="m-tabbed-content">
    <ul class="m-tabs" :class="tabStyle">
      <li v-if="tabTitle" class="m-tabs__tab-title">
        {{ tabTitle }}
      </li>
      <li
        v-for="tab in tabs"
        :key="tab.id"
        :class="selectedTabId === tab.id ? 'm-tabs__tab--active' : ''"
        class="m-tabs__tab"
        role="button"
        @click="selectedTabId = tab.id"
      >
        {{ tab.title }}
      </li>
    </ul>

    <div class="m-tabs__content">
      <component :is="selectedTab.component" v-bind="selectedTab.props" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'MTabbedContent',
  props: {
    tabTitle: {
      type: String,
      required: false,
      default: null,
    },
    tabs: {
      type: Array,
      required: false,
      default() {
        return []
      },
    },
    tabStyle: {
      type: String,
      required: true,
      default: null,
      validator: function(value) {
        // The value must match one of these strings
        return ['m-tabs--subnav', 'm-tabs--button-group'].includes(value)
      },
    },
    buttons: Boolean,
  },
  data() {
    return {
      selectedTabId: this.tabs.length ? this.tabs[0].id : null,
    }
  },
  computed: {
    selectedTab() {
      return this.tabs.find(({ id }) => this.selectedTabId === id)
    },
  },
}
</script>

<style lang="scss">
@import '../sass/framework/framework.scss';

$tab-gutter: $gutter--xs / 2;

.m-tabs {
  margin-top: $pad-v--lg;

  &__tab,
  &__tab-title {
    list-style: none;
  }

  &__tab-title {
    // Margins adding independently to override button styles
    margin-left: $tab-gutter;
    margin-right: $pad-h--sm;

    // Full width title at small views
    @include breakpoint(sm-down) {
      text-align: center;
      width: 100%;
    }
  }

  &__content {
    margin-bottom: $pad-v--sm;
    margin-top: $pad-v--sm;
  }

  // Subnav tab layout
  &--subnav {
    display: flex;
    justify-content: flex-start;
    position: relative;

    .m-tabs__tab {
      @include font--small;

      border-bottom: 1px solid $clay-dark;
      cursor: pointer;
      font-weight: $font-weight--semibold;
      padding-bottom: $pad-v--xxs;
      padding-left: $pad-h--sm;
      padding-right: $pad-h--sm;
      position: relative;
      transition: background $animation-speed ease, color $animation-speed ease;

      @include breakpoint(xs-up) {
        @include font--normal;

        padding-left: $pad-h--lg;
        padding-right: $pad-h--lg;
      }

      &--active,
      &:hover {
        &::before {
          background-color: $color-eire;
          border-radius: 50%;
          bottom: -5px;
          content: '';
          display: block;
          height: 8px;
          left: calc(50% - 4px);
          position: absolute;
          width: 8px;
        }
      }

      &--active {
        color: $grey-mid;
      }
    }
  }

  // Button group tab layout
  &--button-group {
    align-items: center; // to align accompanying text
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    // Set negative margin to allow for card gutters
    margin-left: -$tab-gutter;
    margin-right: -$tab-gutter;

    .m-tabs__tab {
      @include button;
      @include button--secondary;
      // Margins adding independently to override button styles
      margin-left: $tab-gutter;
      margin-right: $tab-gutter;
      margin-top: $pad-v--xxs;

      &--active {
        @include button--active;
        @include button--checked;
      }
    }
  }
}
</style>
