<template>
  <m-card-wrap
    tag="article"
    class="m-features-row__column m-features-item"
    :class="classModifiers"
    :content-orientation="mobSideImage ? 'horizontal-mob' : 'vertical'"
  >
    <div class="m-features-item__img">
      <img
        v-if="image"
        :src="image"
        :alt="title"
        class="m-features-item-image"
        loading="lazy"
      />
    </div>
    <div class="m-features-item__content m-features-item-content">
      <img
        v-if="titleImage"
        :src="titleImage"
        :alt="title"
        class="m-features-item-content__title-image"
        loading="lazy"
      />
      <p v-else-if="title" class="m-features-item-content__title">
        {{ title }}
      </p>
      <div class="m-features-item-content__text" v-html="content" />
      <m-button
        v-if="buttonText && buttonLink"
        :href="buttonLink"
        :target="buttonTarget"
        :data-qa-locator="buttonDataLocator"
        class="m-features-item__btn"
        secondary
        text-cta="responsive"
        size="small"
        tag="a"
      >
        {{ buttonText }}
      </m-button>
    </div>
  </m-card-wrap>
</template>

<script>
import MButton from './MButton.vue'
import MCardWrap from './MCardWrap.vue'

export default {
  name: 'MFeatureCard',
  components: {
    MButton,
    MCardWrap,
  },
  props: {
    image: {
      type: String,
      default: null,
    },
    imagePlacement: {
      type: String,
      default: null,
    },
    title: {
      type: String,
      default: null,
    },
    titleImage: {
      type: String,
      default: null,
    },
    content: {
      type: String,
      required: true,
    },
    buttonText: {
      type: String,
      default: null,
    },
    buttonLink: {
      type: String,
      default: null,
    },
    buttonTarget: {
      type: String,
      default: null,
      required: false,
    },
    buttonDataLocator: {
      type: String,
      default: null,
    },
    mobSideImage: Boolean,
  },
  computed: {
    classModifiers() {
      return {
        'm-features-item--mob-side-image': this.mobSideImage,
      }
    },
  },
}
</script>

<style lang="scss">
@import '../sass/framework/framework.scss';

// Row and column styles in mixins to save duplication
// as the four column layout uses the same styles as the 3 column view,
// but with media queries changing when the different styles get applied.

@mixin feature-row--aligned {
  flex: 1;
  flex-direction: row;
}

@mixin feature-row--stacked {
  display: flex;
  flex-direction: column;
}

@mixin feature-column--sm {
  @include dashed-border-h;

  flex: 1;
  padding: $pad-v--lg 0;
}

@mixin feature-column--lg {
  @include dashed-border-v;

  padding: 0 $pad-h--lg;
}

@mixin feature-column--sm--first {
  padding-top: 0;
}

@mixin feature-column--lg--first {
  padding-left: 0;
}

@mixin feature-column--sm--last {
  background-image: none;
  padding-bottom: 0;
}

@mixin feature-column--lg--last {
  background-image: none;
  padding-right: 0;
}

// Row and column classes and modified layouts.
.m-features-row {
  @include feature-row--stacked;

  @include breakpoint(sm-up) {
    @include feature-row--aligned;
  }

  &__column {
    @include feature-column--sm;

    @include breakpoint(sm-up) {
      @include feature-column--lg;
    }

    @include breakpoint(md-up) {
      padding: 0 $pad-h--xl;
    }

    &:first-child {
      @include feature-column--sm--first;

      @include breakpoint(sm-up) {
        @include feature-column--lg--first;
      }
    }

    &:last-child {
      @include feature-column--sm--last;

      @include breakpoint(sm-up) {
        @include feature-column--lg--last;
      }
    }
  }

  // Four item row, which takes on same layouts,
  // but changes at different media query breakpoints.
  &--four-items {
    @include breakpoint(xs-up) {
      @include feature-row--stacked;
    }

    @include breakpoint(sm-up) {
      @include feature-row--aligned;
    }

    > .m-features-row__column {
      @include breakpoint(xs-up) {
        @include feature-column--sm;
      }

      @include breakpoint(sm-up) {
        @include feature-column--lg;
      }

      &:first-child {
        @include breakpoint(xs-up) {
          @include feature-column--sm--first;
        }

        @include breakpoint(sm-up) {
          @include feature-column--lg--first;
        }
      }

      &:last-child {
        @include breakpoint(xs-up) {
          @include feature-column--sm--last;
        }

        @include breakpoint(sm-up) {
          @include feature-column--lg--last;
        }
      }
    }
  }
}

.m-features-item {
  display: block; // IE 11 fallback

  &__img {
    margin-bottom: $pad-v--lg;
    min-height: 130px;
  }

  &__content {
    flex: 1;
  }

  &__btn {
    align-self: flex-start;
  }

  &--mob-side-image {
    .m-features-item__img {
      min-height: 0;
    }

    @include breakpoint(xs-down) {
      .m-features-item__img {
        margin-bottom: 0;
        margin-right: $pad-h--lg;
        max-width: 5rem;
      }
    }

    @include breakpoint(xxs-only) {
      .m-features-item__img {
        margin-right: $pad-h--sm;
        max-width: 3.5rem;
      }
    }
  }
}

.m-features-item-content {
  display: block; // IE 11 fallback
  display: flex;
  flex-direction: column;

  &__title {
    display: block;
    font-size: 20px; // Not in font scale
    font-weight: $font-weight--semibold;
    line-height: 24px;
    margin-bottom: $pad-v--xs;
  }

  &__title-image {
    align-self: flex-start;
    // Margin bottom less than "title" as text
    // With image taking line height of "title" as text plus
    // the additional amount from the reduce margin
    margin-bottom: $pad-v--xxs;
    max-height: 28px;
  }

  &__text {
    flex: 1;

    > p:last-of-type {
      margin-bottom: 0;
    }
  }
}

.m-features-item-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-height: 130px;
  max-width: 100%;
  width: auto;
}
</style>
