<template functional>
  <hr
    class="m-hr"
    :class="[
      { 'm-hr--dashed': props.dashed },
      { 'm-hr--light': props.light },
      { 'm-hr--padded': props.padded },
      { 'm-hr--short-left': props.shortType === 'left' },
      { 'm-hr--short-center': props.shortType === 'center' },
    ]"
  />
</template>

<script>
export default {
  name: 'MHr',
  props: {
    dashed: {
      type: Boolean,
      default: false,
    },
    light: {
      type: Boolean,
      default: false,
    },
    padded: {
      type: Boolean,
      default: false,
    },
    shortType: {
      type: String,
      default: null,
      validator: value => ['left', 'center'].includes(value),
    },
  },
}
</script>

