<template>
  <div class="m-image-text-block" :class="blockModifiers">
    <div
      class="m-image-text-block__image m-image-promo-overlay"
      :class="imageModifiers"
      :style="backgroundImage"
    >
      <m-promo-sticker
        v-if="promoContent || promoCtaLink || promoCtaText"
        :text-content="promoContent"
        :cta-link="promoCtaLink"
        :cta-text="promoCtaText"
        class="m-image-promo-overlay__sticker"
      />
      <img
        v-if="promoImage"
        :src="promoImage"
        class="m-image-promo-overlay__sticker m-image-promo-overlay__sticker--image"
        :alt="promoImageAlt"
        loading="lazy"
      />
    </div>
    <div class="m-image-text-block__text">
      <div class="text-content">
        <slot name="title">
          <h2 v-if="title">{{ title }}</h2>
        </slot>
        <slot name="content">
          <div v-if="content" v-html="content" />
        </slot>
        <m-button
          v-if="primaryCtaLink && primaryCtaText"
          :href="primaryCtaLink"
          :target="primaryCtaTarget"
          :secondary="primaryCtaOutlined"
          tag="a"
          text-cta
        >
          {{ primaryCtaText }}
        </m-button>

        <template v-if="secondaryCtaLink && secondaryCtaText">
          <m-hr short-type="left" />
          <m-button
            :href="secondaryCtaLink"
            :data-qa-locator="secondaryCtaDataLocator"
            link-style="chevron-right"
            size="micro"
            tag="a"
          >
            {{ secondaryCtaText }}
          </m-button>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import MPromoSticker from './MPromoSticker.vue'
import MButton from './MButton.vue'
import MHr from './MHr.vue'

export default {
  name: 'MImageTextBlock',
  components: {
    MButton,
    MHr,
    MPromoSticker,
  },
  props: {
    title: String,
    content: String,
    introImage: String,
    primaryCtaText: String,
    primaryCtaTarget: String,
    primaryCtaLink: String,
    primaryCtaOutlined: {
      type: Boolean,
      required: false,
      default: false,
    },
    primaryCtaDataLocator: String,
    secondaryCtaText: String,
    secondaryCtaLink: String,
    secondaryCtaDataLocator: String,
    promoContent: String,
    promoCtaText: String,
    promoCtaLink: String,
    promoCtaDataLocator: String,
    promoImage: String,
    promoImageAlt: {
      type: String,
      required: false,
    },
    containImage: Boolean,
    rightAlignImage: Boolean,
    unevenSplit: Boolean,
    longText: Boolean,
    imageMask: Boolean,
    imageHeightMedium: Boolean,
    imageHeightLarge: Boolean,
    flipped: Boolean,
  },
  computed: {
    backgroundImage() {
      return `background-image: url('${this.introImage}')`
    },
    blockModifiers() {
      return {
        'm-image-text-block--uneven-split': this.unevenSplit,
        'm-image-text-block--long-text': this.longText,
        'm-image-text-block--flipped': this.flipped,
      }
    },
    imageModifiers() {
      return {
        'm-image-text-block__image--contain': this.containImage,
        'm-image-text-block__image--right': this.rightAlignImage,
        'm-image-text-block__image--height-medium': this.imageHeightMedium,
        'm-image-text-block__image--height-large': this.imageHeightLarge,
        'm-image-mask-hexagon': this.imageMask,
      }
    },
  },
}
</script>

<style lang="scss">
@import '../sass/framework/framework.scss';

$promo-position: 24px;
$promo-position--mob: 10px;

.m-image-text-block {
  @include breakpoint(xs-up) {
    display: flex;
  }

  &__image,
  &__text {
    @include breakpoint(xs-up) {
      flex: 1;
    }
  }

  &--flipped & {
    &__image {
      // compiles to .m-image-text-block--flipped .m-image-text-block__image
      order: 2;
    }
  }

  &__image {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    @include breakpoint(xs-down) {
      padding-top: 66%; // To set a height and maintain aspect ratio
    }

    @include breakpoint(xxs-up) {
      margin-right: $pad-h--md;
    }

    @include breakpoint(xs-up) {
      margin-right: $pad-h--xl;
    }

    // Modify image height to set height on desktop
    &--height-medium {
      @include breakpoint(sm-up) {
        min-height: 390px;
      }

      @include breakpoint(xl-only) {
        min-height: 450px;
      }
    }

    &--height-large {
      @include breakpoint(sm-up) {
        min-height: 432px;
      }

      @include breakpoint(xl-only) {
        min-height: 500px;
      }
    }

    // Modifiers to image styles
    &--contain {
      background-size: contain;

      @include breakpoint(xxs-only) {
        height: 80vw;
        margin: auto;
        max-height: 350px;
        max-width: 350px;
        padding-top: 0;
        width: 80vw;
      }
    }

    &--right {
      // background position of image to the right on desktop
      @include breakpoint(xs-up) {
        background-position: right center;
      }
    }

    // Modifier to add hexagon shape mask
    &.m-image-mask-hexagon {
      -webkit-mask-image: url('~../assets/svg/hexagon-outline.svg');
      mask-image: url('~../assets/svg/hexagon-outline.svg');
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
    }
  }

  &__text {
    align-items: center;
    display: flex;
    flex: 1;
    margin-bottom: $pad-v--lg;
    margin-top: $pad-v--lg;

    @include breakpoint(xxs-up) {
      margin-left: $pad-h--md;
    }

    @include breakpoint(sm-up) {
      margin-left: $pad-h--lg;
    }
  }

  // A variation where the image is a set width and allows for longer text
  &--long-text {
    @include breakpoint(xs-up) {
      display: flex;
    }

    .m-image-text-block__image {
      height: 290px;
      max-width: 250px;
      padding-top: 0;

      @include breakpoint(sm-down) {
        margin-left: auto;
        margin-right: auto;
      }

      @include breakpoint(xxs-up) {
        max-width: 290px;
        min-height: 330px;
      }

      @include breakpoint(xs-up) {
        margin-right: $pad-h--sm;
      }
    }

    .m-image-text-block__text {
      @include font--small;

      @include breakpoint(xs-up) {
        margin-left: $pad-h--lg;
      }

      .text-content {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  // Increase padding on text area
  &--uneven-split {
    .m-image-text-block {
      &__text {
        @include breakpoint(md-up) {
          flex: 4; // Break away from 50/50 split
          margin-bottom: $pad-v--xxl;
          margin-top: $pad-v--xxl;
        }

        > .text-content {
          padding-right: 0;
        }
      }

      &__image {
        @include breakpoint(md-up) {
          flex: 3; // Break away from 50/50 split
        }
      }
    }
  }
}

.m-image-promo-overlay {
  position: relative;

  &__sticker {
    left: $promo-position--mob;
    position: absolute;
    top: $promo-position--mob;

    @include breakpoint(xs-up) {
      left: $promo-position;
      top: $promo-position;
    }

    &--image {
      max-width: 60px;

      @include breakpoint(xs-up) {
        max-width: 90px;
      }
    }
  }
}

.text-content {
  @include breakpoint(lg-up) {
    padding-right: 20%;
  }

  @include breakpoint(xl-only) {
    padding-right: 30%;
  }
}

// The class that wraps if there are more than one .m-image-text-block.
// The classes below in (even) relate to the flipped version of the block.
.block-wrapper {
  margin-bottom: $pad-v--md;
  margin-left: auto;
  margin-right: auto;
  max-width: 810px;

  @include breakpoint(xs-up) {
    margin-bottom: $pad-v--xl;
  }

  @include breakpoint(md-up) {
    margin-bottom: 80px;
  }

  &:last-of-type {
    margin-bottom: 0;
  }

  &:nth-child(even) {
    .m-image-text-block {
      flex-direction: row-reverse;

      .m-image-text-block__image {
        @include breakpoint(xs-up) {
          margin-left: $pad-h--sm;
          margin-right: 0;
        }
      }

      .m-image-text-block__text {
        @include breakpoint(xs-up) {
          margin-left: 0;
          margin-right: $pad-h--lg;
        }
      }
    }
  }
}
</style>
