<script>
export default {
  name: 'MButton',
  functional: true,
  props: {
    /**
     * href prop shorthand
     * prop is no longer supported and marked as deprecated to provide warning messages to help with migration
     *
     * @deprecated
     */
    btnLink: {
      type: String,
      default: null,
    },
    /**
     * Add custom classes
     * prop is no longer supported and marked as deprecated to provide warning messages to help with migration
     *
     * @deprecated
     */
    modifier: {
      type: [String, Array],
      default: null,
    },
    /**
     * Tag for the component
     * eg. `a`, `button`, `router-link`, `nuxt-link`
     */
    tag: {
      type: String,
      default: 'button',
    },
    /**
     * Use secondary button style
     */
    secondary: {
      type: Boolean,
      default: false,
    },
    /**
     * Use checked button style
     */
    checked: {
      type: Boolean,
      default: false,
    },
    /**
     * Change button link style
     */
    linkStyle: {
      type: String,
      default: null,
      validator: prop =>
        [
          'underline',
          'no-underline',
          'external-link',
          'chevron-left',
          'chevron-right',
          'chevron-up',
          'chevron-down',
        ].includes(prop),
    },
    /**
     * Change default size
     */
    size: {
      type: String,
      default: null,
      validator: prop => ['small', 'micro'].includes(prop),
    },
    /**
     * Change default size
     */
    dropdown: {
      type: Boolean,
      default: false,
    },
    /**
     * Enable Disabled state
     */
    isDisabled: {
      type: Boolean,
      default: false,
    },
    /**
     * Enable Loading state, adds loading animation
     */
    loading: {
      type: Boolean,
      default: false,
    },
    /**
     * Additional class to add when button comes after text
     * Pushes button away from text using margin rule from BBM UI Toolkit
     * Can be either `true` or string `responsive`
     */
    textCta: {
      type: [Boolean, String],
      default: null,
    },
  },
  render(createElement, { props, children, data, listeners }) {
    if (props.btnLink) {
      console.warn(
        'Migration reminder: prop "btn-link" has been removed, update component'
      )
    }
    if (props.modifier) {
      console.warn(
        'Migration reminder: prop "modifier" has been removed, update component'
      )
    }
    const buttonClasses = [
      ...(data.attrs && data.attrs.class ? [data.attrs.class] : []),
      {
        // Base styles
        'm-button': !props.linkStyle,
        'm-button--primary':
          !(props.secondary || props.checked) && !props.linkStyle,
        'm-button--secondary': props.secondary,
        'm-button--checked': props.checked,
        // Link Styles
        'm-button--underline': props.linkStyle === 'underline',
        'm-button--no-underline': props.linkStyle === 'no-underline',
        'm-button--external-link': props.linkStyle === 'external-link',
        'm-button--chevron':
          props.linkStyle && props.linkStyle.includes('chevron'),
        'm-button--chevron-left': props.linkStyle === 'chevron-left',
        'm-button--chevron-right': props.linkStyle === 'chevron-right',
        'm-button--chevron-up': props.linkStyle === 'chevron-up',
        'm-button--chevron-down': props.linkStyle === 'chevron-down',
        // Size Styles
        'm-button--small': props.size === 'small',
        'm-button--micro': props.size === 'micro',
        // Additional Styles
        'm-button--progress': props.loading,
        'm-button--dropdown': props.dropdown,
        'm-button--text-cta': props.textCta,
        'm-button--text-cta-responsive': props.textCta === 'responsive',
      },
    ]
    const enabledListeners = { ...listeners }
    // Button should not allow interactions, if it's in either disabled or loading state
    if (props.isDisabled || props.loading) {
      delete enabledListeners.click // Get rid of the click listeners, if button should be unclickable
    }
    return createElement(
      props.tag,
      {
        ...data,
        attrs: {
          ...data.attrs,
          disabled: props.isDisabled,
        },
        class: buttonClasses,
        on: enabledListeners,
      },
      children
    )
  },
}
</script>

<style lang="scss">
@import '../sass/framework/framework.scss';

.m-button {
  @include button;

  &--primary {
    @include button--primary;

    &.m-button--progress {
      @include button--progress;
      @include button--progress-white;
    }
  }

  &--secondary {
    @include button--secondary;

    &.m-button--progress {
      @include button--progress;
    }
  }

  &--checked {
    @include button--active;
    @include button--checked;
  }

  &--dropdown {
    @include button--dropdown;

    &.m-button--secondary {
      // btn--secondary using grey icon and switch to white on hover
      &::after {
        background-image: url('../assets/svg/icon-chevron-down.svg');
      }

      &:hover::after {
        background-image: url('../assets/svg/icon-white-chevron-down.svg');
      }
    }
  }

  &--chevron {
    @include button;
    @include button--chevron;

    &-right {
      @include button--chevron-right;
    }

    &-left {
      @include button--chevron-left;
    }

    &-down {
      @include button--chevron-down;
    }

    &-up {
      @include button--chevron-up;
    }
  }

  &--underline {
    // This style is applied to all text links as standard
    // in the typography stylesheet

    @include link;

    &.m-button--progress {
      @include button--progress;
    }
  }

  &--no-underline {
    // This removes the standard underline style on links that a
    // aren't deemed as "important" and the style isn't needed
    border-bottom: none;
    color: $link-color;
    transition: color $animation-speed ease;

    &:hover {
      box-shadow: none;
    }
  }

  &--external-link {
    @include font--micro;

    color: $link-color;
    font-weight: $font-weight--semibold;
    text-decoration: none;

    &:hover {
      color: $link-hover-color;
    }

    &::after {
      background: url('../assets/svg/icon-external-link.svg') left bottom
        no-repeat;
      content: '';
      display: inline-block;
      height: $font-size-micro;
      margin-left: $pad-v--xxs;
      width: $font-size-micro;
    }
  }
  // Additional class to add when button comes after text
  // Pushes button away from text using margin rule from BBM UI Toolkit
  &--text-cta {
    margin-top: $pad-v--md;
  }

  &--text-cta-responsive {
    @include breakpoint(xs-down) {
      margin-top: $pad-v--xxs;
    }
  }

  &--small {
    @include font--small;
  }

  &--micro {
    @include font--micro;
  }
}
</style>
