      <img
        v-if="imageMobile"
        :src="imageMobile"
        class="visible-xxs"
        :alt="title ? title : 'Image description'"
      />
      <img
        v-if="image"
        :src="image"
        class="hidden-xxs"
        :alt="title ? title : 'Image description'"
      />
      <div v-if="imageDescription" class="m-block-slot-img-image__description">
        {{ imageDescription }}
      </div>
    </div>
    <div class="m-block-slot-img__slot">
      <slot />
    </div>
  </div>
</template>

<script>
export default {
  name: 'MBlockSlotImageSide',
  props: {
    image: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      required: false,
      default: null,
    },
    imageMobile: {
      type: String,
      required: true,
    },
    imageDescription: {
      type: String,
      required: false,
      default() {
        return null
      },
    },
    imagePosition: {
      required: false,
      type: String,
      default() {
        return 'left'
      },
      validator: function(value) {
        return ['left', 'right'].indexOf(value) !== -1
      },
    },
  },
}
