<template functional>
  <component
    :is="props.tag"
    :class="[
      'm-card-wrap',
      {
        [props.chevron]: !!props.chevron,
        'm-card-wrap--shadow': props.shadow,
        'm-card-wrap--rounded': props.rounded,
        'm-card-wrap--dashed': props.dashed,
      },
      data.staticClass,
      data.class,
    ]"
    v-on="listeners"
  >
    <div
      :class="[
        'm-card-wrap__container',
        `m-card-wrap__container--${props.contentOrientation}`,
        {
          'm-card-wrap__container--centered': props.centeredContent,
          [`m-card-wrap__container--padding-${props.paddingSize}`]: !!props.paddingSize,
          'm-border-radius': props.rounded,
        },
      ]"
    >
      <slot />
    </div>
  </component>
</template>

<script>
export default {
  name: 'MCardWrap',

  props: {
    /**
     * Tag for the component
     * eg. `article`, `div`, `span`, `li`...
     */
    tag: {
      type: String,
      default: 'div',
    },
    /**
     * Adds dashed border to card
     */
    dashed: {
      type: Boolean,
      default: false,
    },
    /**
     * Adds rounded corners to card
     */
    rounded: {
      type: Boolean,
      default: false,
    },
    /**
     * Adds a drop shadow to the card
     */
    shadow: {
      type: Boolean,
      default: false,
    },
    /**
     * Adds chevron on the left or the right side
     */
    chevron: {
      type: String,
      default: null,
      validator: value => ['', 'chevron-left', 'chevron-right'].includes(value),
    },
    /**
     * Adds padding to card
     */
    paddingSize: {
      type: String,
      default: null,
      validator: value => ['xs', 'sm', 'md', 'lg', 'xl', 'xxl'].includes(value),
    },
    /**
     * Centers the card content vertically and horizontally
     */
    centeredContent: {
      type: Boolean,
      default: false,
    },
    /**
     * Controls the Card content orientation
     * eg. `vertical`, `horizontal`
     */
    contentOrientation: {
      type: String,
      default: 'vertical',
      validator: value =>
        ['vertical', 'horizontal', 'horizontal-mob', 'vertical-mob'].includes(
          value
        ),
    },
  },
}
</script>

<style lang="scss">
@import '../sass/framework/framework.scss';
.m-card-wrap {
  background-color: $white;
  border-color: $white;
  box-sizing: border-box;
  display: flex;
  position: relative;
  z-index: 1;

  &--rounded {
    border-radius: 12px;
  }

  &--shadow {
    box-shadow: 0 2px 4px 0 rgba($color-sailor-hover, 0.15);
  }

  &--dashed {
    border: 1px dashed $grey-mid;
  }

  &__container {
    background-color: inherit;
    border-color: inherit;
    border-radius: none;
    display: flex;
    height: 100%;
    overflow: hidden;
    width: 100%;

    &--vertical {
      flex-direction: column;
    }

    &--horizontal {
      flex-direction: row;
    }

    // Horizontal specifically for Mobile, Vertical all others
    &--horizontal-mob {
      flex-direction: column;

      @include breakpoint(xs-down) {
        flex-direction: row;
      }
    }

    // Vertical specifically for Mobile, Horizontal all others
    &--vertical-mob {
      flex-direction: row;

      @include breakpoint(xs-down) {
        flex-direction: column;
      }
    }

    &--centered {
      align-items: center;
      justify-content: center;
      text-align: center;
    }

    &--padding-xs {
      padding: $pad-v--xs;
    }

    &--padding-sm {
      padding: $pad-v--sm;
    }

    &--padding-md {
      padding: $pad-v--md;
    }

    &--padding-lg {
      padding: $pad-v--lg;
    }

    &--padding-xl {
      padding: $pad-v--xl;
    }

    &--padding-xxl {
      padding: $pad-v--xxl;
    }
  }
}

.chevron {
  &-left::before,
  &-right::after {
    border-style: solid;
    content: '';
    height: 0;
    position: absolute;
    top: 20px;
    width: 0;
    z-index: -1;
  }

  &-left::before {
    border-color: transparent;
    border-right-color: inherit;
    border-width: 7px 12px 7px 0;
    left: -11px;
  }

  &-right::after {
    border-color: transparent;
    border-left-color: inherit;
    border-width: 7px 0 7px 12px;
    right: -11px;
  }
}

.m-card-wrap--shadow.chevron-left::before,
.m-card-wrap--shadow.chevron-right::after {
  filter: drop-shadow(0 2px 4px rgba($color-sailor-hover, 0.15));
}

.m-border-radius {
  border-radius: 12px;
}
</style>
