  border-bottom: 1px solid $clay-dark;
  margin-bottom: $pad-v--xs;
  padding: $pad-v--xs 0 $pad-v--xs / 2;
  position: relative;
  text-align: left;

  &:last-of-type {
    margin-bottom: 0;
  }

  @include breakpoint(sm-up) {
    padding: $pad-v--sm 0 $pad-v--sm;
  }

  &--chevron {
    background: url('../assets/svg/icon-eire-chevron-down.svg') center no-repeat;
    border: none;
    cursor: pointer;
    height: $chevron-size;
    position: absolute;
    right: 0;
    top: $pad-v--sm;
    transition: transform $animation-speed ease;
    width: $chevron-size;
  }

  .chevron--close {
    bottom: $pad-v--md;
    transform: rotateX(180deg);
    transition: transform $animation-speed;
  }

  &--title {
    cursor: pointer;
    font-weight: $font-weight--semibold;
    margin-right: $chevron-size * 1.5;
    text-align: left;
  }

  &--subtitle {
    color: $color-sailor;
    font-size: $font-size-small;
    text-decoration: none;
  }

  &--posted {
    color: $grey-mid;
    font-size: $font-size-micro;
  }

  & .item {
    overflow: hidden;
    transition: $animation-speed ease-in-out;

    &-inner {
      &--content {
        @include font--small;

        padding-top: $pad-v--sm;
      }

      &--links {
        padding-top: $pad-v--sm;

        a {
          margin-right: $pad-h--xxl;
        }
      }
    }
  }
}

// INFO BLOCK USED AT THE END OF ACCORDION
// HAS SAME DESIGN AS THE ACCORDION ITEM

.m-accordion-wrapper {
  .more-info {
    border-bottom: 1px solid $clay-dark;
    padding: $pad-v--lg 0 $pad-v--sm;

    &__title {
      @include font--micro;
      margin-bottom: 0;
    }

    &__link {
      @include font--micro;
      margin-right: 1rem;

      &:last-of-type {
        margin-right: 0;
      }

