      },
    },
    title: {
      type: String,
      required: true,
      default() {
        return null
      },
    },
    content: {
      type: String,
      required: true,
      default() {
        return null
      },
    },
    buttonText: {
      type: String,
      required: true,
      default() {
        return null
      },
    },
    buttonLink: {
      type: String,
      required: false,
      default() {
        return null
      },
    },
    videoLink: {
      type: String,
      default() {
        return null
      },
    },
  },
  methods: {
    handleClick(link) {
      this.$emit('player-open', link)
    },
  },
}
