    margin-bottom: $pad-v--xs;
  }

  .footer {
    bottom: -10px;
    opacity: 0.5;
    position: absolute;
    right: -10px;
  }
}

.card-arrow {
  &-container {
    display: flex;
    height: 100%;
    overflow: hidden;
    position: absolute;
    right: -20px;
    width: 90px;
  }

  &-down {
    align-self: center;
    background-image: url('../assets/svg/icon-chevron-down.svg');
    background-position: center center;
    background-repeat: no-repeat;
    cursor: pointer;
    height: 90px;
    position: absolute;
    right: 0;
    -webkit-transition: -webkit-transform $animation-speed ease-in-out;
    -ms-transition: -ms-transform $animation-speed ease-in-out;
    transition: transform $animation-speed ease-in-out;
    width: 100%;
    z-index: 2;
  }

  &-up {
    -ms-transform: rotateX(-180deg);
    -webkit-transform: rotateX(-180deg);
    transform: rotateX(-180deg);
  }
}

