<template>
  <div class="m-banner">
    <div v-if="heading || text || primaryButtonText" class="m-banner__content">
      <div class="container">
        <div class="m-banner__content__meta">
          <m-heading-one :heading="heading" />
          <div v-if="text">
            {{ text }}
          </div>

          <m-button
            v-if="primaryButtonText && primaryButtonLink"
            :href="primaryButtonLink"
            :target="primaryButtonTarget"
            :data-qa-locator="primaryButtonDataLocator"
            size="small"
            tag="a"
            text-cta
          >
            {{ primaryButtonText }}
          </m-button>

          <!-- Secondary Button with HR -->
          <div
            v-if="secondaryButtonText && secondaryButtonLink"
            class="hidden-xxs"
          >
            <m-hr short-type="left" />
            <m-button
              :href="secondaryButtonLink"
              :target="secondaryButtonTarget"
              :data-qa-locator="secondaryButtonDataLocator"
              link-style="chevron-right"
              size="micro"
              tag="a"
            >
              {{ secondaryButtonText }}
            </m-button>
          </div>
        </div>
      </div>
    </div>

    <div class="m-banner__media m-banner-media">
      <!-- Banner background image (responsive images set in methods) -->
      <div :style="banner" class="m-banner-media__image" />

      <!-- Overlay images -->
      <div class="m-banner-media__overlay">
        <!-- Limited to 4 items, as more than 4 will look bad and
        flow beyond the width of the banner on mobile views -->
        <img
          v-for="(overlay, index) in overlays.slice(0, 4)"
          :key="index"
          :src="overlay.source"
          :alt="overlay.altText"
          loading="lazy"
        />
        <div v-if="textBox" class="m-banner-textbox">
          {{ textBox }}
        </div>
      </div>

      <!-- Show a quote and/or image -->
      <div v-if="quote" class="m-banner-quote hidden-xxs hidden-xs">
        <div class="container">
          <blockquote>&ldquo;{{ quote }}&rdquo;</blockquote>
          <img
            v-if="quoteImage"
            :src="quoteImage"
            :alt="quoteAltText"
            loading="lazy"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MButton from './MButton.vue'
import MHeadingOne from './MHeadingOne.vue'
import MHr from './MHr.vue'
import debounce from 'lodash.debounce'

export default {
  name: 'MBanner',
  components: {
    MButton,
    MHeadingOne,
    MHr,
  },
  props: {
    wideBannerImage: {
      type: String,
      required: false,
    },
    regularBannerImage: {
      type: String,
      required: true,
    },
    mobileBannerImage: {
      type: String,
      required: false,
    },
    heading: String,
    text: String,
    textBox: String,
    primaryButtonText: String,
    primaryButtonLink: String,
    primaryButtonTarget: String,
    primaryButtonDataLocator: {
      type: String,
      default: 'header-primary-button',
    },
    secondaryButtonText: String,
    secondaryButtonLink: String,
    secondaryButtonTarget: String,
    quote: String,
    quoteImage: String,
    quoteAltText: String,
    secondaryButtonDataLocator: {
      type: String,
      default: 'header-secondary-button',
    },
    overlays: {
      type: Array,
      required: false,
      default() {
        return []
      },
    },
  },
  data() {
    return { banner: '' }
  },
  mounted() {
    this.resizeBanner()
    window.addEventListener('resize', this.resizeBanner)
  },
  methods: {
    // Set regularBannerImage as default banner with added mobile & wide options
    resizeBanner: debounce(function() {
      if (window.innerWidth >= 1920 && this.wideBannerImage) {
        this.banner = `background-image: url('${this.wideBannerImage}');`
      } else if (window.innerWidth < 1024 && this.mobileBannerImage) {
        this.banner = `background-image: url('${this.mobileBannerImage}');`
      } else {
        this.banner = `background-image: url('${this.regularBannerImage}');`
      }
    }, 200),
  },
}
</script>

