<template>
  <m-card-wrap shadow rounded>
    <div class="settings-card">
      <section :class="`settings-card__header icon__${icon}`">
        <h4 v-if="title">{{ title }}</h4>
      </section>

      <section class="settings-card__body">
        <h4 v-if="title">{{ title }}</h4>
        <slot />
        <div
          v-if="isExpandable"
          class="arrow-down"
          :class="{ 'arrow-up': localOpen }"
          :aria-label="i18n('click_to_expand', 'Click to expand card content')"
          tabindex="0"
          @click="localOpen = !localOpen"
          @keypress.space="localOpen = !localOpen"
          @keypress.enter="localOpen = !localOpen"
        />

        <collapse-transition v-if="isExpandable">
          <div v-if="localOpen" class="settings-card__body-content">
            <slot name="collapsable-content" />
          </div>
        </collapse-transition>
      </section>
    </div>
  </m-card-wrap>
</template>

<script>
import MCardWrap from './MCardWrap.vue'
import { CollapseTransition } from 'vue2-transitions'
import i18nMixin from '../mixins/i18n'

export default {
  name: 'MSettingsCard',

  components: {
    MCardWrap,
    CollapseTransition,
  },

  mixins: [i18nMixin],

  props: {
    icon: {
      type: String,
      default: null,
    },
    title: {
      type: String,
      default: null,
    },
    open: {
      type: Boolean,
      default: false,
    },
    isExpandable: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      localOpen: false,
    }
  },

  watch: {
    open: {
      handler(newValue) {
        if (newValue !== this.localOpen) this.localOpen = newValue
      },
      immediate: true,
    },
    localOpen(newValue) {
      this.$emit('update:open', newValue)
    },
  },
}
</script>

