      <!-- mobile navigation -->
      <nav
        class="m-mobile-menu"
        :class="{ 'm-mobile-menu--show': mobileNavDisplay }"
      >
        <div class="m-mobile-menu__top">
          <m-close-button @clicked="mobileNavDisplay = false"></m-close-button>

          <ul class="m-nav m-nav--dark">
            <li v-if="!session">
              <a href="/account/" data-qa-locator="header-mobile-login-link">
                Existing customers
              </a>
            </li>

            <template v-if="session">
              <li>
                <a
                  href="/account/"
                  data-qa-locator="header-mobile-quotes-policies-link"
                >
                  My quotes & policies
                </a>
              </li>
              <li>
                <a
                  href="/account/#/settings/"
                  data-qa-locator="header-mobile-settings-link"
                >
                  Settings
                </a>
              </li>
            </template>
            <li>
              <a href="/contact/" data-qa-locator="header-mobile-contact-link">
                Help
              </a>
            </li>
            <template v-if="session">
              <li>
                <a
                  href="/account/logout/"
                  data-qa-locator="header-mobile-logout-button"
                >
                  <b>Log out</b>
                </a>
              </li>
            </template>
          </ul>
        </div>

        <div class="m-mobile-menu__bottom">
          <ul class="m-nav">
            <li>
              <a href="/" data-qa-locator="header-mobile-home-link">
                Home
              </a>
            </li>
            <li>
              <a
                href="/pet-insurance/"
                data-qa-locator="header-mobile-pet-policies-link"
              >
                Pet policies
              </a>
            </li>
            <li>
              <a href="/news/" data-qa-locator="header-mobile-pet-guides-link">
                Pet guides
              </a>
            </li>
            <li>
              <a href="/about/" data-qa-locator="header-mobile-about-us-link">
                About us
              </a>
            </li>
          </ul>
        </div>
      </nav>
    </div>

    <!-- Desktop header -->
    <header
      :class="{
        'm-site-header': true,
        'm-site-header--overlay': overlay,
        'm-site-header--overlay--fade':
          overlay && overlayFade && overlayFadeComplete,
      }"
      :style="overlayFadeStyle"
    >
      <div class="container">
        <div class="m-header-items">
          <a
            href="/"
            class="m-header-items__logo"
            data-qa-locator="header-logo-link"
          >
            <span class="sr-only">Bought By Many</span>
          </a>
          <nav
            v-if="mode === 'full'"
            class="m-header-items__menu menu-nav"
            role="presentation"
          >
            <ul class="m-header-menu">
              <li class="m-header-menu__item hidden-xxs hidden-xs">
                <a
                  href="/pet-insurance/"
                  data-qa-locator="header-pet-policies-link"
                >
                  Pet policies
                </a>
              </li>
              <li class="m-header-menu__item hidden-xxs hidden-xs">
                <a href="/news/" data-qa-locator="header-pet-guides-link">
                  Pet guides
                </a>
              </li>
              <li class="m-header-menu__item hidden-xxs hidden-xs">
                <a href="/about/" data-qa-locator="header-pet-about-us-link">
                  About us
                </a>
              </li>
            </ul>

            <button
