              :class="{
                'm-account-btn': true,
                'm-account-btn--dropdown': session,
                'm-account-btn--dropdown--selected': displayDropdown,
              }"
              data-qa-locator="header-login-logout-button"
              @click="handleAccountBtn"
            >
              <template v-if="session">
                My account
              </template>
              <template v-else>
                Existing customers
              </template>
            </button>

            <div
              v-if="session && displayDropdown"
              class="m-dropdown hidden-xs hidden-xxs"
            >
              <ul>
                <li>
                  <a
                    href="/account/"
                    data-qa-locator="header-quotes-policies-link"
                  >
                    My quotes & policies
                  </a>
                </li>
                <li>
                  <a
                    href="/account/#/settings/"
                    data-qa-locator="header-settings-link"
                  >
                    Settings
                  </a>
                </li>
                <li>
                  <a href="/contact/" data-qa-locator="header-contact-link">
                    Help
                  </a>
                </li>
                <li class="highlight">
                  <a
                    href="/account/logout/"
                    data-qa-locator="header-logout-button"
                  >
                    Log out
                  </a>
                </li>
              </ul>
            </div>
            <m-burger
              data-qa-locator="header-burger-menu-button"
              @clicked="mobileNavDisplay = true"
            ></m-burger>
          </nav>

          <!-- No nav, Telephone only -->
          <a
            v-else-if="mode === 'simple' && telephoneNumber"
            class="m-header-items__telephone"
            :href="`tel:+${formatTelephoneNumberLink(telephoneNumber)}`"
            data-qa-locator="header-telephone-button"
          >
            <span>{{ telephoneNumber }}</span>
          </a>

          <!-- No nav, CTA only -->
          <nav
            v-else-if="mode === 'simpleCta' && ctaOnlyLink && ctaOnlyText"
            class="m-cta-nav"
          >
            <m-button
              :href="ctaOnlyLink"
              class="m-cta-btn m-cta-nav__btn"
              tag="a"
              :data-qa-locator="ctaOnlyDataLocator"
              :title="ctaOnlyText"
            >
              {{ ctaOnlyText }}
            </m-button>
          </nav>

          <!-- No nav, CTA & Tel only -->
          <nav v-else-if="mode === 'simpleCtaTel'" class="m-cta-nav">
