            </a>
            <m-button
              v-if="ctaOnlyLink && ctaOnlyText"
              :href="ctaOnlyLink"
              class="m-cta-btn m-cta-nav__btn"
              tag="a"
              :data-qa-locator="ctaOnlyDataLocator"
              :title="ctaOnlyText"
            >
              {{ ctaOnlyText }}
            </m-button>
